.legacy {
  .api-docs {
  	padding-top: 145px;
  }

  .open-api-docs-tab {
    color: #ef308f;
    box-shadow: none;
  }

  .api-docs-types-link {
    color: #ef308f;
    text-decoration: underline !important; // This is dirty as hell, but necessary to override a third-party stylesheet
  }

  .api-docs-types-link:hover,
  .api-docs-types-link:active,
  .api-docs-types-link:focus {
    text-decoration: none !important; // This is dirty as hell, but necessary to override a third-party stylesheet
  }
}
